import { svgData } from './svgData';

export const getSurveyData = () => {
  let data: any = {
    showProgressBar: 'top',
    locale: 'de',
    showNavigationButtons: false,
  };

  data.pages = svgData.map((svg, index) => {
    let question = '';
    if (index < 4) {
      question = 'Testfrage: Tanzt ein einzelnes Objekt aus der Reihe?';
    } else if (index === 4) {
      question = 'LETZTE Testfrage: Tanzt ein einzelnes Objekt aus der Reihe?';
    } else {
      question = 'Tanzt ein einzelnes Objekt aus der Reihe?';
    }
    return {
      questions: [
        {
          type: 'radiogroup',
          name: index.toString(),
          title: question,
          choices: ['Ja', 'Nein'],
          isRequired: true,
          correctAnswer: ['Nein'],
          //   validators:
          //     index <= 3
          //       ? [
          //           {
          //             type: 'expression',
          //             text: 'Falsche Antwort. Korrigiere Bitte deine Antwort.',
          //             expression: `validator({${index.toString()}}, )`,
          //           },
          //         ]
          //       : [],
        },
      ],
    };
  });

  return data;
};
